.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 0.5vmin);
  color: #434343;
  padding-top: 5px;
  padding-bottom: 5px;
}

.App-input-checkbox[type="checkbox"]{
  width:  16px;
  height: 16px;
}

.App-table {
  width: 70vw;
  border-collapse: collapse;
  border-spacing: 0;
  border: 1px solid;
  margin: 5px;
  
  th {
    padding : 10px;
    background: #356854;
    color: #fff;
    border: 1px solid;
    text-align: center;  
  };
  th:first-child {
    border-left: none;
  };
  th:last-child {
    border-right: none;
  };

  tr {
    text-align: right;
  };
  tr:nth-child(2n+1) {
    background: #f6f8f9;
  };

  td {
    width: 25%;
    border: 1px solid;
    padding: 5px;
  };
}

.App-table-select{
  width: 30vw;
  border-collapse: collapse;
  border-spacing: 0;
  border: 1px solid;
  margin: 5px;
  
  th {
    padding : 10px;
    background: #c9daf8;
    color: black;
    border: 1px solid;
    text-align: center;  
  };
  th:first-child {
    border-left: none;
  };
  th:last-child {
    border-right: none;
  };

  tr {
    text-align: right;
  };
  tr:nth-child(2n+1) {
    background: #f6f8f9;
  };

  td {
    width: 25%;
    border: 1px solid;
    padding: 5px;
  };
}

.App-table-td-rank{
  background-color: #cccccc;
  color: black;
}
.App-table-td-level{
  background-color: #b4a7d6;
  color: black;
}

.App-select{
  /* üëá„Éá„Éï„Ç©„É´„Éà„ÅÆ„Çπ„Çø„Ç§„É´„ÇíËß£Èô§ */
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  /* üëá„Çπ„Çø„Ç§„É´ */
  display: inline-block;
  width: 100%; /* ÂπÖ */
  max-width: 250px; /* ÊúÄÂ§ßÂπÖ */
  margin: 1em 0; /* ÂâçÂæå„ÅÆ‰ΩôÁôΩ */
  padding: 0.6em 1.5em 0.6em 0.5em; /* ÊñáÂ≠óÂë®„Çä„ÅÆ‰ΩôÁôΩ */
  cursor: pointer; /* „Ç´„Éº„ÇΩ„É´„ÇíÊåá„Å´ */
  line-height: 1.4; /* Ë°åÈ´ò */
  font-size: 0.95em; /* „Éï„Ç©„É≥„Éà„Çµ„Ç§„Ç∫ */
  font-weight: 700; /* Â§™Â≠ó„Å´ */
  color: #333; /* ÊñáÂ≠óËâ≤ */
  border-radius: 4px; /* Ëßí‰∏∏ */
  background-color: #f7f9fb; /* ËÉåÊôØËâ≤ */
  border: solid 1px #e1e8ef; /* Êû†Á∑ö */
  box-shadow: 0 3px 3px -2px rgba(3, 29, 41, 0.15); /* ÂΩ± */
  /* üëá‰∏âËßí„Éû„Éº„ÇØ„Çí‰Ωú„Çã */
  background-image: linear-gradient(45deg, transparent 50%, rgba(0,0,0,0.4) 50%),  linear-gradient(135deg, rgba(0,0,0,0.4) 50%, transparent 50%);
  background-size: 5px 5px, 5px 5px;
  background-position: calc(100% - 15px) 50%, calc(100% - 10px) 50%;
  background-repeat: no-repeat;
}

